.hamburger,
.hamburger::before,
.hamburger::after{
	content: '';
	display: block;
	background: #eaebea;
	height: 3px;
	width: 1.75em;
	border-radius: 3px;
	-webkit-transition: all ease-in-out 500ms;
	transition: all ease-in-out 500ms;

}
.hamburger::before{
	-webkit-transform: translateY(-6px);
	        transform: translateY(-6px);

}
.hamburger::after{
	-webkit-transform: translateY(3px);
	        transform: translateY(3px);
}
